.mBanDef {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 450px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat !important;
  color: #fff;

  @include media-breakpoint-down(md) {
    height: 300px;
  }

  @include media-breakpoint-down(sm) {
    height: 200px;
  }

  &.right {
    justify-content: flex-end;
  }

  &__block {
    width: 50%;
    padding-left: 90px;

    @include media-breakpoint-down(md) {
      padding-left: 80px;
    }

    @include media-breakpoint-down(sm) {
      width: 100%;
      text-align: center;
    }

    &.right {
      padding-left: 0;
      padding-right: 90px;

      @include media-breakpoint-down(md) {
        padding-right: 80px;
      }
    }
  }

  &__title {
    font-size: 30px;
    line-height: 33px;
    font-weight: bold;
    //text-shadow: 0 0 10px rgba(#000, 0.3);
    text-shadow: 1px 1px 0 #000;

    @include media-breakpoint-down(sm) {
      font-size: 24px;
      line-height: 27px;
    }
  }

  &__desc {
    //text-shadow: 0 0 10px rgba(#000, 0.3);
    text-shadow: 1px 1px 0 #000;
  }
}
