.page-about {
  font-size: 21px;
  line-height: 31px;

  @include media-breakpoint-down(lg) {
    font-size: 18px;
    line-height: 28px;
  }

  .img-rounded {
    @include media-breakpoint-down(md) {
      border-radius: 10px;
    }
  }

  .wrapper_inner {
    background: #f9f9fa url('/local/build/img/pages/about/bg.jpg') top no-repeat;
    background-size: contain;
  }

  &__container {
    padding: 4rem 0;
    color: #141414;
  }

  &__header {
    // margin-bottom: 4rem;
  }

  &__name {
    font-size: 3.6rem;
    line-height: 4rem;
    font-weight: bold;
    margin-bottom: 2rem;

    @include media-breakpoint-down(md) {
      font-size: 2.4rem;
      line-height: 3rem;
      text-align: left;
    }

    h1 {
      margin: 0;
      padding: 0;
      font-size: inherit;
      line-height: inherit;

      @include media-breakpoint-down(md) {
        text-align: inherit;
      }
    }
  }

  &__desc {
    font-size: 2rem;
    font-weight: bold;
  }

  &__row {
    margin-top: 4rem;

    &--reorder {
      @include media-breakpoint-down(md) {
        .row {
          display: flex;
          flex-direction: column;

          div[class*=col-]:first-child {
            // order: 2;
          }

          div[class*=col-]:last-child {
            order: -1;
          }
        }
      }
    }
  }

  &__block-title {
    margin: 4rem 0;
    font-size: 2.4rem;
    font-weight: bold;
  }

  &__list {
    margin-top: 3rem;

    &-item {
      display: flex;
      align-items: center;
      margin: 3rem 0;

      &:hover {
        img {
          transform: scale(1.2);
        }
      }
    }

    &-icon {
      width: 10rem;

      img {
        width: 10rem;
        height: 10rem;
        border-radius: 50%;
        border: 2px solid #eee;
        transition: all ease .3s;
      }
    }

    &-text {
      flex: 1;
      margin-left: 3rem;
    }
  }

  &__feed-back {
    margin-top: 3rem;
    margin-left: -30px;
    margin-right: -30px;

    @include media-breakpoint-down(sm) {
      margin-top: 2rem;
      margin-left: -15px;
      margin-right: -15px;
    }
  }
}

.block-test-drive {
  background: #e2e9f1 url('/local/build/img/pages/about/td-bg.jpg') bottom right no-repeat;
  margin-left: -30px;
  margin-right: -30px;
  padding: 50px 30px;

  @include media-breakpoint-down(sm) {
    margin-left: -15px;
    margin-right: -15px;
  }

  &__pictures {
    margin-top: -9rem;
    margin-bottom: -9rem;

    @include media-breakpoint-down(md) {
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  &__picture {
    margin-top: 5rem;

    @include media-breakpoint-down(sm) {
      margin-top: 2rem;
    }

    &:first-child {
      margin-top: 0;

      @include media-breakpoint-down(md) {
        margin-top: 4rem;
      }
    }

    &:last-child {
      @include media-breakpoint-down(sm) {
        display: none;
      }
    }
  }

  &__content-item {
    margin-top: 5rem;

    &:first-child {
      margin-top: 0;
    }
  }

  &__title {
    font-size: 2.4rem;
    line-height: 3rem;
    font-weight: bold;
    margin-bottom: 3rem;
    // margin-top: 3rem;
  }

  &__form {
    margin-top: 3rem;
  }
}
