/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.slick-arrow {
  position: absolute;
  display: block;
  top: 50%;
  width: 40px;
  height: 40px;
  font-size: 0;
  border: none;
  border-radius: 50%;
  z-index: 100; }
  @media (max-width: 767.98px) {
    .slick-arrow {
      display: none !important; } }

.slick-prev {
  left: 20px;
  background: #fff url("/bitrix/templates/aspro_next/images/arrows.png") 14px -96px no-repeat; }
  .slick-prev:hover {
    background: #1976d2 url("/bitrix/templates/aspro_next/images/arrows.png") 14px 10px no-repeat; }

.slick-next {
  right: 20px;
  background: #fff url("/bitrix/templates/aspro_next/images/arrows.png") -20px -96px no-repeat; }
  .slick-next:hover {
    background: #1976d2 url("/bitrix/templates/aspro_next/images/arrows.png") -20px 10px no-repeat; }

.slick-dots {
  position: absolute;
  left: 50%;
  bottom: 10px;
  margin: 0;
  padding: 0;
  list-style: none;
  transform: translateX(-50%); }
  .slick-dots li {
    display: inline-block;
    margin: 0;
    padding: 0 2px;
    list-style: none; }
    .slick-dots li:before {
      display: none; }
    .slick-dots li.slick-active button {
      background-color: #1976d2; }
    .slick-dots li button {
      display: block;
      width: 14px;
      height: 14px;
      font-size: 0;
      border-radius: 50%;
      background-color: transparent;
      border: 3px solid #1976d2;
      box-shadow: 1px 1px 0 white; }

