.mBanLena {
  //padding: 20px 0 40px 0;
  background-color: #e5ecf4;
  background-image: url('/local/build/img/showcase/lena/bg.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat !important;

  @include media-breakpoint-down(sm) {
    background-image: url('/local/build/img/showcase/lena/bg-sm.jpg');
    background-position: right;
  }

  * {
    color: #141414;
  }

  .wrapper_inner {
    position: relative;
    background: url('/local/build/img/showcase/lena/bg-girl.png') right bottom no-repeat;
    background-size: contain;

    @include media-breakpoint-down(sm) {
      background: url('/local/build/img/showcase/lena/bg-girl-sm.png') right top no-repeat;
      //background-color: red;
    }
  }

  &__head {
    margin-top: 50px;
    font-size: 32px;
    line-height: 42px;
    font-weight: 600;

    @include media-breakpoint-down(sm) {
      font-size: 24px;
    }
  }

  &__desc {
    margin: 30px 0;
    font-size: 16px;

    p {
      margin-bottom: 4px;
    }
  }

  &__icons {
    margin-left: 50px;
    margin-bottom: 30px;

    @include media-breakpoint-down(sm) {
      margin-left: 0;
    }

    &-md {
      @include media-breakpoint-down(sm) {
        display: none;
      }
    }

    &-sm {
      display: none;

      @include media-breakpoint-down(sm) {
        display: block;
      }
    }

    img {
      width: 100%;
      height: auto;
    }
  }

  &__btn {
    position: relative;
    display: inline-block;
    padding: 15px 40px;
    border-radius: 4px;
    color: #222222;
    white-space: nowrap;
    background-color: #ffd200;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -18px;
      // top: 50%;
      width: 22px;
      height: 54px;
      background: url('/local/build/img/ico/btn-arrow.png') top left no-repeat;
      background-size: contain;
      // transform: rotate(45deg);
      /*margin-top: -25px;
      background-color: red;
      border-radius: 4px;
      */
    }

    &:hover {
      // background-color: rgba(#ffd200, .8);
      transform: scale(1.05);
      transition: all ease .3s;
    }
  }

  &__footer {
    margin-bottom: 50px;
  }
}

@include media-breakpoint-down(sm) {
  .mBanLena__icons-sm {
    display: block;
  }
}
