.mFeedBack {

  &.bg {
    color: #fff;
    background-color: #a02c9d !important;
    background-image: url('/local/build/img/feed-back/bg.jpg') !important;
    background-position: center !important;
    background-size: cover !important;
    background-repeat: no-repeat !important;

    @include media-breakpoint-down(sm) {
      background-position: right !important;
    }
  }

  .title_block {
    color: inherit;
  }

  .maxwidth-theme {
    padding-top: 30px;
    padding-bottom: 30px;
    background: none;
  }

  &__desc {
    font-size: 16px;
    margin: 20px 0;
  }

  &__send {
    display: none;

    h4 {
      margin: 50px 0 10px 0;
      color: inherit;
    }
  }
}

.mFeedBackForm {
  display: flex;
  flex-wrap: wrap;

  @include media-breakpoint-down(sm) {
    flex-direction: column;
  }

  &__field {
    display: block;
    width: 50%;
    padding: 10px 0;

    @include media-breakpoint-down(sm) {
      width: 100%;
    }

    &:nth-child(odd) {
      padding-right: 10px;

      @include media-breakpoint-down(sm) {
        padding-right: 0;
      }
    }

    &:nth-child(even) {
      padding-left: 10px;

      @include media-breakpoint-down(sm) {
        padding-left: 0;
      }
    }
  }

  &__input {
    width: 100%;
    border-width: 2px !important;

    &:focus {
      border-color: #ffd200;
    }
  }

  &__label {
    position: relative;
    margin: 0;
    width: 100%;

    label.error {
      right: 8px;
    }
  }

  &__footer {
    margin-top: 20px;
  }

  &__submit {
    position: relative;
    padding: 15px 30px;
    font-size: 16px;
    line-height: 24px;
    border: none;
    border-top-left-radius: 6px;
    border-bottom-left-radius: 6px;
    background-color: #ffd200;

    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: -20px;
      // top: 50%;
      width: 22px;
      height: 54px;
      background: url('/local/build/img/ico/btn-arrow.png') top left no-repeat;
      background-size: contain;
      // transform: rotate(45deg);
      /*margin-top: -25px;
      background-color: red;
      border-radius: 4px;
      */
    }

    &:hover {
      //background-color: rgba(#ffd200, .7);
      transform: scale(1.05);
      transition: all ease .3s;
    }
  }
}
