.mAboutIndex {
  font-size: 16px;
  background-color: #f9f9fa;

  .maxwidth-theme {
    background: none;
    padding-top: 0 !important;
  }

  .sections_wrapper {
    padding-bottom: 0 !important;
  }

  &__text {
    padding: 30px 0;

    h3 {
      margin-bottom: 20px;
    }

    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__team {
    &-block {
      position: relative;
      padding: 30px 0;

      @include media-breakpoint-down(sm) {
        padding-top: 0;
        padding-bottom: 15px;
      }
    }
  }
}
