.slick-arrow {
    position: absolute;
    display: block;
    top: 50%;
    width: 40px;
    height: 40px;
    font-size: 0;
    border: none;
    border-radius: 50%;
    //background-size: 15px;
    z-index: 100;

    @include media-breakpoint-down(sm) {
        display: none !important;
    }
}

.slick-prev {
    left: 20px;
    background: #fff url('/bitrix/templates/aspro_next/images/arrows.png') 14px -96px no-repeat;

    &:hover {
        background: #1976d2 url('/bitrix/templates/aspro_next/images/arrows.png') 14px 10px no-repeat;
    }
}
.slick-next {
    right: 20px;
    background: #fff url('/bitrix/templates/aspro_next/images/arrows.png') -20px -96px no-repeat;

    &:hover {
        background: #1976d2 url('/bitrix/templates/aspro_next/images/arrows.png') -20px 10px no-repeat;
    }
}


.slick-dots {
    position: absolute;
    left: 50%;
    bottom: 10px;
    margin: 0;
    padding: 0;
    list-style: none;
    transform: translateX(-50%);

    li {
        display: inline-block;
        margin: 0;
        padding: 0 2px;
        list-style: none;

        &:before {
            display: none;
        }

        &.slick-active {
            button {
                background-color: #1976d2;
            }
        }

        button {
            display: block;
            width: 14px;
            height: 14px;
            font-size: 0;
            border-radius: 50%;
            background-color: transparent;
            border: 3px solid #1976d2;
            box-shadow: 1px 1px 0 rgba(#fff,5);
        }
    }
}
