
.mAdvantages {
  background-color: #1a76d2 !important;
  background-image: url('/local/build/img/advantages/bg.png') !important;
  background-position: center !important;
  background-size: cover !important;
  background-repeat: no-repeat !important;

  @include media-breakpoint-down(md) {
    background-position: left !important;
  }

  .title_block {
    color: #fff;
    margin-bottom: 40px;
  }

  .maxwidth-theme {
    background: none;
  }

  &__list {

    .row {
      &:before,
      &:after {
        display: block;
      }
    }
    //margin-left: -45px;
    //margin-right: -45px;
  }
}

.maIcon {
  color: #fff;


  @include media-breakpoint-down(md) {
    margin: 30px 0;
  }

  &__image {
    text-align: center;
    img {
      width: 140px;
      height: 140px;
      border-radius: 50%;

      @include media-breakpoint-down(md) {
        width: 100px;
        height: 100px;
      }
    }
  }

  &__desc {
    margin-top: 24px;
    font-size: 16px;
    text-align: center;

    b {
      color: inherit;
    }
  }

  &:hover {
    img {
      transform: scale(1.1);
      transition: all ease .3s;
    }
  }
}
