.image_wrapper_block,
.image_wrapper_block a {
  height: 230px !important;
}

.mBlockDlm {
  border-top: 1px solid #eee;
}

.mTitleBlock {
  display: flex;
  align-items: center;
  margin: 20px 0;

  &__youtube {
    padding-left: 40px;
    background: url('/local/build/img/ico/youtube.png') no-repeat;
    background-size: contain;
  }

  &__instagram {
    display: block;
    //padding-left: 40px;
    width: 170px;
    height: 35px;
    margin-right: 20px;
    background: url('/local/build/img/ico/instagram.png') no-repeat;
    background-size: contain;
  }
}


.sections_wrapper .list .item:not(.section_item) .img {
  @include media-breakpoint-down(xs) {
    height: 100px !important;
    line-height: 100px !important;
  }
}
